<template>
  <div :class="accordionClasses">
    <div v-for="(item, index) in items" :key="index" :class="itemClasses" @click="toggle(index)">
      <div class="accordion-header flex justify-between items-center cursor-pointer">
        <h3 :class="headerClasses">{{ item.title }}</h3>
        <span :class="chevronClasses">{{ isOpen(index) ? '-' : '+' }}</span>
      </div>
      <div v-if="isOpen(index)" class="accordion-content p-4">
        <slot :name="`content-${index}`">{{ item.content }}</slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Accordion',
  props: {
    items: {
      type: Array,
      required: true,
    },
    variant: {
      type: String,
      default: 'default', // default, bordered, highlighted, collapsible
    },
  },
  data() {
    return {
      openIndex: null,
    };
  },
  computed: {
    accordionClasses() {
      return [
        'accordion',
        'space-y-2',
      ];
    },
    itemClasses() {
      const baseClasses = [
        'accordion-item',
        'transition-all',
        'duration-300',
        'ease-in-out',
      ];
      const variantClasses = {
        default: 'bg-white border border-grey-light',
        bordered: 'bg-grey-lighter border border-grey-dark',
        highlighted: 'bg-intrepid-red-light border border-intrepid-red-dark text-white',
        collapsible: 'bg-midnight-light border border-midnight-dark text-white',
      };
      return [...baseClasses, variantClasses[this.variant] || variantClasses.default];
    },
    headerClasses() {
      return [
        'accordion-header',
        'py-2',
        'px-4',
        'font-semibold',
      ];
    },
    chevronClasses() {
      return [
        'accordion-chevron',
        'text-xl',
        'transition-transform',
      ];
    },
  },
  methods: {
    toggle(index) {
      this.openIndex = this.openIndex === index ? null : index;
    },
    isOpen(index) {
      return this.openIndex === index;
    },
  },
};
</script>

<style scoped>
.accordion-item:hover {
  @apply bg-grey-lighter-alpha;
}
</style>
